.weather-container {
    background: #3949ab;
    box-shadow: 2px 4px 16px 0 rgba(0, 0, 0, 0.5);
    height: 625px;
    width: 80%;
    border-radius: 5px;
    position: relative;
    padding: 10px;
    text-align: center;
  
    @media screen and (min-width: 700px) {
      height: 350px;
    }
  
    @media screen and (min-width: 1100px) {
      width: 60%;
    }
  
    @media screen and (min-width: 1350px) {
      width: 50%;
    }
  
    @media screen and (min-width: 1500px) {
      width: 40%;
    }
  }
  
  .weather-header {
    @media screen and (min-width: 700px) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 25px;
    }
  
    h3 {
      font-size: 1.4rem;
      font-weight: 500;
      letter-spacing: 2px;
      text-align: center;
  
      @media screen and (min-width: 700px) {
        margin: 0;
      }
    }
  
    div {
      display: flex;
      border-bottom: 2px solid #eee;
      width: 80%;
      margin: auto;
  
      @media screen and (min-width: 700px) {
        width: 50%;
        margin: 0;
      }
    }
  
    button {
      border: none;
      background: transparent;
      color: #17a589;
      cursor: pointer;
      outline: none;
    }
  }
  
  .search-input {
    background: transparent;
    border: none;
    padding: 4px;
    outline: none;
    width: 100%;
    color: #d5d8dc;
    font-size: 1.2rem;
  }
  
  .search-input::-webkit-input-placeholder {
    font-size: 1.1rem;
    color: #eee;
    opacity: 0.5;
  }
  
  .weather-info {
    display: flex;
    flex-direction: column;
    height: 300px;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    margin-top: 100px;
  
    @media screen and (min-width: 700px) {
      height: 100px;
      margin-top: 65px;
    }
  
    i {
      color: yellow;
      position: relative;
      top: 5px;
      left: 5px;
      animation: rotate 5s infinite linear;
    }
  
    h3 {
      margin-bottom: 0;
    }
  }
  
  @keyframes rotate {
    to {
      transform: rotate(360deg);
    }
  }
  
  .error {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 0;
    color: #ec7063;
    font-size: 1.3rem;
  }
  
  .header-description {
    text-align: center;
  }
  
  .degree-symbol::after {
    content: '\00b0';
  }
  
  .weather-data-flex {
    @media screen and (min-width: 700px) {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      width: 100%;
    }
  }